.q-qsc__hero-container {
	height: 402px;
}
.q-qsc__paragraph-section {
	max-width: 39em;
}
.q-qsc__about-section b {
	font-weight: 500;
}
.link {
	color: #2E8BE0;
	transition: color 0.35s;
	text-decoration: none;
}
.q-qsc__hero-content .q-button,
.q-qsc__hero-content .hero-signup-info {
	display: none;
}
.q-form__label.hidden,
.mktoForm label,
.mktoInstruction,
.mktoForm .qmktoRadioListLabel,
.mktoForm .qmktoCheckboxListLabel {
	display: block;
	font-size: 15px;
	font-weight: 500;
	color: #627281;
	padding-right: 1.5em;
	min-width: 160px;
}
.mktoCheckboxList label {
	text-align: left;
	width: auto;
	font-size: 15px !important;
}
.mktoFieldWrap {
	display: flex;
	flex-direction: column;
}
.q-form__button,
.mktoButton,
.mktoButton:last-child,
.mktoNextButtonRow {
	background-color: #ED2E26;
	height: 80px;
	width: 100%;
}
.q-form__button:hover,
.mktoButton:hover,
.mktoButton:last-child:hover,
.mktoNextButtonRow:hover,
.button:hover {
	background-color: #E3241C;
}
.mktoBackButtonRow:hover {
	background-color: transparent;
	cursor: pointer;
}
.mktoBackButtonRow {
	display: flex;
	align-items: center;
	position: absolute;
	left: 0;
	top: 1.5em;
}
.mktoBackButtonRow .arrow {
	margin-right: 0.8em;
}
.mktoBackButtonRow .button-label {
	font-weight: 500;
	font-size: 18px;
}
.q-form__button-group, .mktoButtonRow .mktoButtonWrap {
	margin: 0;
}
.panel .buttons-wrapper {
	margin-bottom: 1em;
}
.buttons-wrapper {
	display: flex;
	justify-content: space-around;
	margin: 2em 0;
	position: relative;
}
.fancybox-slide--iframe .fancybox-content {
	width: 600px;
	max-width: 80%;
	max-height: 80%;
	margin: 0;
	max-width: 600px;
}
.fancybox-slide--iframe .fancybox-close-small,
.fancybox-slide--image .fancybox-close-small {
	right: 0;
}
.fancybox-close-small:after {
	display: none;
}
.fancybox-button:focus,
.fancybox-button:hover {
	color: inherit;
	opacity: 0.5;
}
.q-lp-privacy-info {
	text-align: center;
	font-size: 14px;
}
.q-qsc__about-section {
	margin-bottom: 6em;
	font-family: "Gotham", sans-serif;
}
.steps {
	font-family: "Gotham", sans-serif;
	font-size: 18px;
	display: flex;
	justify-content: space-between;
	border-bottom: 2px solid #F2F2F3;
	margin-bottom: 2em;
	margin-top: 2.5em;
	flex-direction: column;
	display: none;
}
.step {
	margin-bottom: 1em;
	padding-bottom: 0.8em;
	display: block;
}
.step.active {
	font-weight: 500;
	border-bottom: 3px solid #6A778B;
}
.mktoErrorMsg {
	margin-left: 0;
}
.panel.training .button,
.panel.sessions .button {
	width: 45%;
	padding: 0px;
}
.panel.training .mktoBackButtonRow,
.panel.sessions .mktoBackButtonRow {
	padding-left: 0;
	width: auto;
}
.panel.training .buttons-wrapper,
.panel.sessions .buttons-wrapper {
	justify-content: flex-end;
}
.steps br {
	display: none;
}
.mktoInstruction {
	display: none;
}
.mktoForm .qmktoRadioListLabel, .mktoForm .qmktoCheckboxListLabel {
	margin-bottom: 0;
}
label#LblFirstName:before,
label#LblLastName:before,
label#LblEmail:before,
label#LblTitle:before,
label#LblCompany:before,
label#LblPhone:before,
label#LblRegistration_Type__c_lead:before,
label#LblRegistration_Type__c:before,
label#LblTraining_Session__c:before,
label#LblConference_Sessions__c:before,
label#Lblquestions:before,
label#LblMktoPersonNotes:before,
label#LblCountry:before,
label#LblState:before,
label#LblCompany_Size_Range__c_account:before {
	content: "* ";
	color: #DC412C;
}
.panel.training .mktoFieldWrap,
.panel.sessions .mktoFieldWrap {
	display: block;
}
.panel.training .mktoFormCol,
.panel.sessions .mktoFormCol {
	display: flex;
	flex-direction: column;
}
#LblTraining_Session__c,
#LblConference_Sessions__c {
	display: block;
	width: auto;
	text-align: left;
}
.mktoForm .qmktoRadioListLabel, .mktoForm .qmktoCheckboxListLabel {
	margin-top: 0;
}
.q-form {
	margin-top: 3em;
}
[id^="LblmktoRadio"],
[id^="LblmktoCheckbox"] {
	margin-top: 1em;
	margin-bottom: 1em;
}
.mktoFormRow.Registration_Type__c_lead {
	margin-top: 2em;
}
.Conference_Sessions__c .mktoCheckboxList label,
.Training_Session__c .mktoCheckboxList label {
	color: #262626;
	font-weight: 500;
}
.Conference_Sessions__c .mktoCheckboxList li,
.Training_Session__c .date-time {
	font-weight: 400;
	color: #69768D;
	margin-bottom: -0.3em;
}
.Training_Session__c .date-time {
	display: inline-block;
	text-indent: 2em;
}
.Conference_Sessions__c .mktoCheckboxList li:before {
	content: "-";
	margin-left: -5px;
	text-indent: -5px;
	display: block;
	float: left;
}
.Conference_Sessions__c .mktoCheckboxList ul {
	list-style: none;
	margin-top: 0;
}
.sessions .check-all-none a,
.training .check-all-none a {
    display: inline;
    margin: 0;
}
.sessions .check-all-none a:hover,
.training .check-all-none a:hover {
    cursor: pointer;
}
.check-all-none .pipe {
	display: inline-block;
	margin: 0 0.5em;
}
.mktoForm .qmktoRadioListLabel, .mktoForm .qmktoCheckboxListLabel {
    width: auto !important;
    text-align: left !important;
}
.mktoRadioList.mktoHasWidth.mktoRequired {
    display: flex;
}
.mktoForm .mktoRadioList label {
	width: auto !important;
	min-width: 0;
}
.mktoForm label {
	text-align: left !important;
}
#LblcommunicationConsent {
	display: none;
}
.mktoFormRow.questions {
    margin-top: 2em;
}
@media (min-width:620px) {
	.q-qsc__about-section {
		margin-top: -4.4em;
	}
	.step {
		margin-bottom: 0;
	}
	.steps {
		margin-bottom: 2.8em;
	}
	.steps br {
		display: block;
	}
	.steps .pipe {
		display: none;
	}
	.mktoFieldWrap {
		flex-direction: row;
	}
	.q-form__button,
	.mktoButton,
	.mktoButton:last-child,
	.mktoNextButtonRow,
	.button {
		max-width: 406px;
	}
	.buttons-wrapper {
		margin: 2em 0;
	}
	.mktoErrorMsg {
		margin-left: 30px;
	}
	.panel.training .buttons-wrapper,
	.panel.sessions .buttons-wrapper {
		justify-content: space-around;
	}
	.q-form__input, input[type=text].mktoField,
	input[type=email].mktoField,
	input[type=tel].mktoField,
	textarea.mktoField,
	select.mktoField {
		max-width: 410px;
		width: 300px;
	}
	.steps {
		flex-direction: row;
	}
	.q-form__label.hidden, .mktoForm label, .mktoInstruction, .mktoForm .qmktoRadioListLabel, .mktoForm .qmktoCheckboxListLabel {
		text-align: right;
	}
	.mktoForm [id^="LblmktoCheckbox"],
	.mktoForm [id^="LblmktoRadio"] {
		text-align: left;
	}
	.mktoForm .qmktoRadioListLabel, .mktoForm .qmktoCheckboxListLabel {
		margin-bottom: 1em;
	}
	.q-form__label.hidden,
	.mktoForm label,
	.mktoInstruction,
	.mktoForm .qmktoRadioListLabel,
	.mktoForm .qmktoCheckboxListLabel {
		width: 165px;
		min-width: 0;
	}
	.mktoForm [id^="LblmktoCheckbox"],
	.mktoForm [id^="LblmktoRadio"] {
		width: auto;
	}
	[id^="LblmktoRadio"],
	[id^="LblmktoCheckbox"] {
		margin-top: 0;
		margin-bottom: 0.5em;
	}
	.sessions [id^="LblmktoCheckbox"],
	.training [id^="LblmktoCheckbox"] {
		margin-bottom: 0.8em;
	}
}
@media (min-width:720px) {
	.q-qsc__hero-container {
		height: 602px;
	}
}
@media (min-width:680px) {
	.steps br {
		display: none;
	}
	.steps .pipe {
		display: inline;
	}
	.panel.training .mktoFormCol,
	.panel.sessions .mktoFormCol {
		flex-direction: row;
		align-items: center;
	}
}
@media (min-width:920px) {
	.steps {
		display: flex;
	}
	.q-form__label.hidden, .mktoForm label, .mktoInstruction, .mktoForm .qmktoRadioListLabel, .mktoForm .qmktoCheckboxListLabel {
		min-width: 187px;
	}
	.q-form__input,
	input[type=text].mktoField,
	input[type=email].mktoField,
	input[type=tel].mktoField,
	textarea.mktoField,
	select.mktoField {
		min-width: 410px;
	}
	.q-form__label.hidden,
	.mktoForm label,
	.mktoInstruction,
	.mktoForm .qmktoRadioListLabel,
	.mktoForm .qmktoCheckboxListLabel {
		width: 180px;
		min-width: 180px;
	}
}
#conduct {
margin-bottom: 7em;
}

@media (min-width:900px) {
	.mktoForm .mktoRadioList label{
		display: flex !important;
	}
	#LblqSCsession1, #LblqSCsession2, #LblqSCsession3, #LblqSCsession4, #LblqSCday1{
		width: 50% !important;
		min-width: 50% !important
	}	
}
@media (max-width:899px) {
	.mktoForm .mktoRadioList label{
		display: flex !important;
	}
	#LblqSCsession1, #LblqSCsession2, #LblqSCsession3, #LblqSCsession4, #LblqSCday1{
		width: 100% !important;
		min-width: 100% !important
	}	
	.LblqSCsession1, .LblqSCsession2, .LblqSCsession3, .LblqSCsession4, .LblqSCday1{
		width: 100% !important;
		min-width: 100% !important
	}	
	.mktoFieldWrap{
		display: block;
	}
	
}

